import React from "react"
import Link from "gatsby-link"
import styles from "./css/site.css"


const ListLink = props =>
    <li>
        <Link to={props.to}>
            {props.children}
        </Link>
    </li>

const today = new Date();

export default ({ children, data }) =>
    <div>
        <nav className="navbar navbar-default navbar-fixed-top">
            <div className="container">
                <div className="navbar-header">
                    <button type="button" className="navbar-toggle collapsed" data-toggle="collapse" data-target="#navbar" aria-expanded="false" aria-controls="navbar">
                        <span className="sr-only">Toggle navigation</span>
                        <span className="icon-bar"></span>
                        <span className="icon-bar"></span>
                        <span className="icon-bar"></span>
                    </button>
                    <Link to="/" className="navbar-brand" style={{color: 'black'}}>RETURN OF THE SHADOW</Link>
                </div>
                <div id="navbar" className="navbar-collapse collapse">
                    <ul className="nav navbar-nav">
                        <ListLink to="/">HOME</ListLink>
                        <ListLink to="/news">NEWS</ListLink>
                        <ListLink to="/connect">CONNECT</ListLink>
                        <ListLink to="/logs">LOGS</ListLink>
                        <ListLink to="/rcd">RCD</ListLink>
                        <ListLink to="/docs">DOCS</ListLink>
                        <ListLink to="/about">ABOUT</ListLink>
                    </ul>
                </div>
            </div>
        </nav>
        {children()}
        <div className="footer-container">
            <footer className="footer">
                <div className="container">
                    <p className="text-muted">&copy; {today.getFullYear()} Return of the Shadow, Inc.</p>
                </div>
            </footer>
        </div>
    </div>

    export const query = graphql`
        query LayoutQuery {
            site {
                siteMetadata {
                    title
                }
            }
        }
    `


